import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

import { getUserState } from "../../data/user/UserActions";

class GlobalMessage extends React.Component {
  componentDidMount() {
    const { getUserState } = this.props;
    getUserState();
  }

  render() {
    const { userState, customMessage } = this.props;

    return (
      <div className="global-message">
        {userState.stored_results_need_upload &&
          !customMessage && (
            <Link to="statistics" className="global-message-link">
              You have analyses that are not uploaded to the database. <br />
              Click here to upload.
            </Link>
          )}
        {userState.stored_results_need_upload &&
          customMessage && (
            <Link to="statistics" className="global-message-link">
              {customMessage}
            </Link>
          )}
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    userState: state.user.user_state,
  };
}

export default connect(mapStateToProps, { getUserState })(GlobalMessage);



