import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import logo from "../../resources/logo.png";
import wifi from "../../resources/wifi.png";
import wifiRed from "../../resources/wifi-red.png";
import battery from "../../resources/battery.png";
import power from "../../resources/power.png";
import back from "../../resources/back.png";

import { getAccounts, setAccount } from "../../data/user/UserActions";

class AccountsPage extends React.Component {
  constructor(props) {
    super(props);

    this.goBack = this.goBack.bind(this);
  }

  componentDidMount() {
    const { getAccounts } = this.props;
    getAccounts();
  }

  async chooseClicked(e) {
    e.preventDefault();
    const { setAccount } = this.props;
    const { id, name } = JSON.parse(this.refs.accountSelect.value);
    await setAccount(id, name);
    if (this.props.active_account) {
      this.props.history.push("user-options");
    }
  }

  goBack(ev) {
    ev.preventDefault();
    this.props.history.goBack();
  }

  render() {
    const { active_account, network_status } = this.props;

    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img
          className="login-logo-barcode"
          src={logo}
          alt="Logo"
          styles={{ width: "40%" }}
        />
        <img
          className="battery-alt"
          src={battery}
          alt="Battery"
          styles={{ width: "40%" }}
        />
        <Link to="selectnetwork">
          <img
            className="wifi-signal"
            src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi"
            styles={{ width: "40%" }}
          />
        </Link>
        <Link to="poweroff">
          <img
            className="power"
            src={power}
            alt="Power"
            styles={{ width: "40%" }}
          />
        </Link>
        <form>
          <div className="form-title" style={{ left: "300px" }}>
            Choose an account:
          </div>
          <div className="select" style={{ top: "168px", left: "287px" }}>
            <select ref="accountSelect">
              {this.props.accounts.map((a) => (
                <option key={a.id} value={JSON.stringify(a)}>
                  {a.name}
                </option>
              ))}
            </select>
            <div className="select_arrow"></div>
          </div>
          {this.props.accounts.length && (
            <input
              className="submit-button"
              type="submit"
              value="Choose"
              onClick={(e) => this.chooseClicked(e)}
            />
          )}
          <hr className="hr-login" />
        </form>
        <a onClick={this.goBack}>
          <img
            className="back-button"
            src={back}
            alt="Back"
            styles={{ width: "40%" }}
          />
        </a>
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    accounts: state.user.accounts,
    active_account: state.user.active_account,
    network_status: state.user.network_status,
  };
}

export default connect(mapStateToProps, { getAccounts, setAccount })(
  AccountsPage
);



