import React from 'react';
import {Link} from 'react-router-dom';
import { connect } from "react-redux";
import logo from '../../resources/logo.png';
import back from '../../resources/back.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import contact from '../../resources/contact.png';
import guide from '../../resources/guide.png';
import GlobalMessage from '../globalmessage/GlobalMessage';

class GuidePage extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.backClick = this.backClick.bind(this);
  }

  backClick() {
    this.props.history.goBack();
  }

  render() {
    const { network_status } = this.props;

    return (
      <div>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{width:'40%'}} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{width:'40%'}} />
          <Link to="selectnetwork"><img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi" styles={{width:'40%'}} /></Link>
          <Link to="poweroff"><img className="power" src={power} alt="Power"
            styles={{width:'40%'}} /></Link>

          <Link to="nutrienttesting"><img src={guide} alt="Guide" className="guide"
          styles={{width:'40%'}} /></Link>
          <span className="guide-caption">Testing Guide</span>

          <Link to="nutrienttesting"><img src={contact} alt="Contact" className="contact"
          styles={{width:'40%'}} /></Link>
        <span className="contact-caption">Contact Us</span>

      <a onClick={this.backClick}><img className="back-button" src={back} alt="Back"
          styles={{width:'40%'}} /></a>
        <GlobalMessage />
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    network_status: state.user.network_status,
  };
}

export default connect(mapStateToProps)(GuidePage);



