import React from 'react';
import logo from '../../resources/welcome.png';

class WelcomePage extends React.Component {
  componentDidMount() {
    console.log(this.props);
    setTimeout(() => {
      this.props.history.push('selectnetwork');
    }, 5000);
  }
  render() {
    return (
      <div>
        <img className="welcome-logo" src={logo} alt="Logo" styles={{width:'40%'}} />
      </div>
    );
  }
}

export default WelcomePage;



