import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Keyboard from 'react-virtual-keyboard';
import { startAnalysis, resetUploadStatus } from '../../data/analysis/AnalysisActions.js';
import { fetchOptions } from "../../data/user/UserActions";
import Popup from '../popup/Popup.js';
import logo from '../../resources/logo.png';
import back from '../../resources/back.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import KSquare from '../../resources/K_Analysis.png';
import NSquare from '../../resources/N_Analysis.png';
import PSquare from '../../resources/P_Analysis.png';
import Progress from 'antd/lib/progress';
import GlobalMessage from '../globalmessage/GlobalMessage.js';

class AnalysePage extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      saved_changes: false,
      is_analyze_next: false,
      show_barcode_modal: false,
      should_show_barcode_modal: false,
      request_sample: ''
    }

    this.analyse = this.analyse.bind(this);
    this.upload = this.upload.bind(this);
    this.analysis = this.analysis.bind(this);
    this.goToResults = this.goToResults.bind(this);
    this.closePopup = this.closePopup.bind(this);
    this.requestSampleChanged = this.requestSampleChanged.bind(this);
    this.saveBarcodeModal = this.saveBarcodeModal.bind(this);
    this.onBackClicked = this.onBackClicked.bind(this);
    this.saveChangesOkClicked = this.saveChangesOkClicked.bind(this);
  }

  async componentDidMount() {
    const { fetchOptions } = this.props;


    if (this.props.options.length === 0) {
      await fetchOptions();
    }

    const option = this.props.options.find(o => o.option === 'Use_barcode');
    if (option && option.default === 2) {
      this.setState(old => ({ ...old, should_show_barcode_modal: true }));
    }
  }

  analyse() {
    console.log("****in analyse()");
    const { startAnalysis } = this.props;
    startAnalysis()
    .then(() => {
      const option = this.props.options.find(o => o.option === 'Use_barcode');
      // If use_barcode is false we skip barcode phase
      if (option && option.default === 2) {
        this.props.history.push('analysis');
        return;
      }

      this.props.history.push('barcode')
    });
  }

  upload() {
    const { should_show_barcode_modal } = this.state;
    if (should_show_barcode_modal) {
      this.setState(old => ({ ...old, show_barcode_modal: true }))
      return;
    }
    this.props.history.push({
      pathname: 'upload',
      search: `?new_sample=${false}`,
    })
  }

  analysis(event) {
    event.preventDefault();

    console.log("****in analysis()");

    if (this.props.analysis_finished) {
      this.setState(old => ({ ...old, saved_changes: true, is_analyze_next: true }));
      return;
    }

    this.analyse();
  }

  saveBarcodeModal() {
    const { request_sample } = this.state;

    this.props.history.push({
      pathname: 'upload',
      search: `?new_sample=${false}&sample_id=${request_sample}`,
    })
  }

  goToResults(event) {
    event.preventDefault();
    this.props.history.push('statistics');
  }

  closePopup() {
    this.setState(old => ({ ...old, saved_changes: false, show_barcode_modal: false }));
  }

  requestSampleChanged(value) {
    this.setState(old => ({
      ...old,
      request_sample: value
    }))
  }

  onBackClicked(event) {
    event.preventDefault();

    if (this.props.analysis_finished) {
      this.setState(old => ({ ...old, saved_changes: true }));
      return;
    }

    this.props.history.push('nutrienttesting');
  }

  saveChangesOkClicked() {
    const { is_analyze_next } = this.state;
    if (is_analyze_next) {
      this.analyse();
      return;
    }

    const { resetUploadStatus } = this.props;
    resetUploadStatus();
    this.props.history.push('nutrienttesting');
  }

  render() {
    const { active_account, network_status } = this.props;
    const { saved_changes, show_barcode_modal, request_sample } = this.state;

    let no3_value = "NO3"
    let no2_value = "NH4"
    let p_value = "P"
    let k_value = "K"

    let no3_percentage = 100
    let no2_percentage = 100
    let p_percentage = 100
    let k_percentage = 100

    if (this.props.analysis_finished) {
      no3_value = this.props.analysis_data["N1"].toFixed(0);
      no2_value = this.props.analysis_data["N2"].toFixed(0);
      p_value = this.props.analysis_data["P"].toFixed(0);
      k_value = this.props.analysis_data["K"].toFixed(0);

      no3_percentage = (no3_value - 10) / 390 * 100;
      no2_percentage = (no2_value - 2) / 28 * 100;
      p_percentage = (p_value - 10) / 90 * 100;
      k_percentage = (k_value - 10) / 390 * 100;

      if(no3_percentage < 0) no3_percentage = 0;
      if(no2_percentage < 0) no2_percentage = 0;
      if(p_percentage < 0) p_percentage = 0;
      if(k_percentage < 0) k_percentage = 0;

      if(no3_percentage > 100) no3_percentage = 100;
      if(no2_percentage > 100) no2_percentage = 100;
      if(p_percentage > 100) p_percentage = 100;
      if(k_percentage > 100) k_percentage = 100;
    }

    let no3_text = <div style={{width: "40px", textAlign: "center"}}>{no3_value}</div>;
    let no2_text = <div style={{width: "40px", textAlign: "center"}}>{no2_value}</div>;
    let p_text = <div style={{width: "40px", textAlign: "center"}}>{p_value}</div>;
    let k_text = <div style={{width: "40px", textAlign: "center"}}>{k_value}</div>;

    let wifi_link;
    let power_link;
    if (this.props.analysis_finished) {
      wifi_link = <img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
        alt="Wifi" styles={{ width: '40%' }} />;
      power_link = <img className="power" src={power} alt="Power"
            styles={{ width: '40%' }} />;
    } else {
      wifi_link = <Link to="selectnetwork"><img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
          alt="Wifi" styles={{ width: '40%' }} /></Link>;
      power_link = <Link to="poweroff"><img className="power" src={power} alt="Power"
          styles={{ width: '40%' }} /></Link>;
    }


    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{ width: '40%' }} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{ width: '40%' }} />
        {wifi_link}
        {power_link}
        <input className="analyse-button" type="button"
          value="Analyze" onClick={this.analysis} />

        <img src={NSquare} alt="N" className="N" styles={{ width: '40%' }} />
        <img src={PSquare} alt="P" className="P" styles={{ width: '40%' }} />
        <img src={KSquare} alt="K" className="K" styles={{ width: '40%' }} />

        <Progress className="NO3" width={60} type="circle" percent={no3_percentage} format={() => no3_text} />

        <span className="analyse-caption-nitrate">Nitrate (ppm)</span>

        <Progress className="NO2" width={60} type="circle" percent={no2_percentage} format={() => no2_text} />

        <span className="analyse-caption-nitrite">Ammonium<br/>(ppm)</span>

        <Progress className="Pcircle" width={60} type="circle" percent={p_percentage} format={() => p_text} />
        <span className="analyse-caption-p">Phosphate (ppm)</span>

        <Progress className="Kcircle" width={60} type="circle" percent={k_percentage} format={() => k_text} />
        <span className="analyse-caption-k">Potassium (ppm)</span>
        {this.props.analysis_finished &&
          <input className="upload-button" type="button" value="Save" onClick={this.upload} />
        }
        <a href="" onClick={this.onBackClicked}>
          <img className="back-button" src={back} alt="Back" styles={{ width: '40%' }} />
        </a>
        <GlobalMessage />

        {saved_changes && (
          <Popup
            message={"You have not saved your last analysis. Press Ok to continue or Cancel to return to last screen."}
            onClick={this.saveChangesOkClicked}
            onCancleClick={this.closePopup} />
        )}
        {show_barcode_modal && (
          <Popup
            message={"Please enter your sample_id"}
            okLabel={"Save"}
            onClick={this.saveBarcodeModal}
            style={{top: '60px'}}
            inputComponent={
              <Keyboard
                placeholder="Enter your sample_id here"
                className="form-field request-sample"
                value={request_sample}
                name="request-sample"
                options={{
                  type: 'text',
                  usePreview: false,
                  layout: 'qwerty',
                  autoAccept: true,
                  alwaysOpen: false,
                  appendLocally: false,
                  updateOnChange: true,
                  color: 'light',
                  enterNavigation: true,
                  keyBinding: 'touchstart',
                }}
                onChange={this.requestSampleChanged}
              />
            }
            onCancleClick={this.closePopup}
            />
        )}
        </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    analysis_started: state.analysis.started,
    analysis_finished: state.analysis.finished,
    analysis_data: state.analysis.result,
    options: state.user.options,
    active_account: state.user.active_account,
    network_status: state.user.network_status,
  };
}

export default connect(mapStateToProps, { startAnalysis, fetchOptions, resetUploadStatus })(AnalysePage);



