import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { Redirect } from "react-router-dom";
import { bindActionCreators } from "redux";
import {
  resetUploadStatus,
  uploadResults,
} from '../../data/analysis/AnalysisActions.js';
import NewSampleModal from './NewSampleModal';
import Case8Modal from './Case8Modal';
import logo from '../../resources/logo.png';
import back from '../../resources/back.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import Popup from '../popup/Popup.js';
import GlobalMessage from '../globalmessage/GlobalMessage.js';

const queryKeywords = {
  true: true,
  false: false,
  null: null,
  undefined,
}

class UploadPage extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      showNewSampleModal: false,
      showCase1Case2Modal: false,
      showCase3Case4Case5Modal: false,
      showCase8Modal: false,
    }

    this.handlePopupClick = this.handlePopupClick.bind(this);
    this.handlePopupCancel = this.handlePopupCancel.bind(this);
    this.handleYesClicked = this.handleYesClicked.bind(this);
    this.handleNewSampleModalSave = this.handleNewSampleModalSave.bind(this);
    this.handleCase8ModalSave = this.handleCase8ModalSave.bind(this);
    this.handleCase8ModalCancel = this.handleCase8ModalCancel.bind(this);
  }

  async componentDidMount() {
    const { resetUploadStatus } = this.props;
    resetUploadStatus();

    // Parse querystring
    const pairs = this.props.location.search.slice(1).split('&');
    var result = {};
    pairs.forEach(function(pair) {
        pair = pair.split('=');
        let value = pair[1]
        if (pair[1] in queryKeywords){
          value = queryKeywords[pair[1]]
        }
        result[pair[0]] = value;
    });

    const { uploadResults } = this.props;
    await uploadResults(result);


    const { uploadData } = this.props;
    this.setState(old => ({
      ...old,
      showCase1Case2Modal: uploadData.case === 'case1' || uploadData.case === 'case2',
      showCase3Case4Case5Modal: uploadData.case === 'case3' || uploadData.case === 'case4' || uploadData.case === 'case5' || uploadData.case === 'case6'
    }));
  }

  handlePopupClick() {
    this.props.history.push('statistics');
  }

  async handlePopupCancel() {
    const { uploadData, uploadResults } = this.props;

    if (uploadData.sample_id) {
      await uploadResults({
        new_sample: false,
        sample_id: uploadData.sample_id,
        store_local: true
      });
    } else {
      await uploadResults({
        new_sample: false,
        store_local: true
      });
    }

    // await uploadResults({
    //   new_sample: false,
    //   sample_id: uploadData.sample_id ? uploadData.sample_id : null,
    //   store_local: true
    // });

    this.props.history.push('statistics');
  }

  async handleYesClicked() {
    const { uploadData, uploadResults } = this.props;
    if (uploadData.case === 'case1') {
      this.setState(old => ({
        ...old,
        showNewSampleModal: true,
        showCase1Case2Modal: false,
      }))
      return;
    }

    await uploadResults({
      new_sample: true,
      sample_id: uploadData.sample_id
    });
    this.props.history.push('statistics');
  }

  async handleNewSampleModalSave(data) {
    const { uploadResults } = this.props;

    console.log('****uploadData radioValue', data.radioValue)
    if (data.radioValue === 'barcode') {
      await uploadResults({
        new_sample: true
      });
      this.setState(old => ({
        ...old,
        showNewSampleModal: false,
        showCase3Case4Case5Modal: true
       }));
      return;
    }

    await uploadResults({
      new_sample: true,
      sample_id: data.inputValue
    });

    // If the response of upload is case 8
    const { uploadData } = this.props;
    console.log('****uploadData.case', uploadData.case)
    if (uploadData.case === 'case8') {
      this.setState(old => ({
        ...old,
        showCase8Modal: true,
        showCase3Case4Case5Modal: false,
        showCase1Case2Modal: false,
        showNewSampleModal: false,
      }));
    }

    // If the response of upload is case 5
    if (uploadData.case === 'case5') {
      this.setState(old => ({
        ...old,
        showCase8Modal: false,
        showCase3Case4Case5Modal: true,
        showCase1Case2Modal: false,
        showNewSampleModal: false,
      }));
    }

    // If the response of upload is case 5
    if (uploadData.case === 'case6') {
      this.setState(old => ({
        ...old,
        showCase8Modal: false,
        showCase3Case4Case5Modal: true,
        showCase1Case2Modal: false,
        showNewSampleModal: false,
      }));
    }

  }

  async handleCase8ModalSave(sample_id) {
    const { uploadResults } = this.props;
    await uploadResults({
      new_sample: true,
      sample_id
    });

    // If the response of upload is case 8
    const { uploadData } = this.props;
    if (uploadData.case === 'case8') {
      this.setState(old => ({
        ...old,
        showCase8Modal: true,
        showCase3Case4Case5Modal: false,
        showCase1Case2Modal: false,
        showNewSampleModal: false,
      }));
      return;
    }

    this.setState(old => ({
      ...old,
      showCase8Modal: false,
    }));
    this.props.history.push('statistics');
  }

  async handleCase8ModalCancel() {
    const { uploadResults } = this.props;
    await uploadResults({
      new_sample: true,
      store_local: true
    });

    this.setState(old => ({
      ...old,
      showCase8Modal: false,
    }));
    this.props.history.push('statistics');
  }

  render() {
    const { uploadData, active_account, error, network_status } = this.props;
    const { showNewSampleModal, showCase1Case2Modal, showCase3Case4Case5Modal, showCase8Modal } = this.state;

    if (error) {
      return <Redirect to='/nutrienttesting' />;
    }

    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img
          className="login-logo-barcode"
          src={logo}
          alt="Logo"
          styles={{ width: '40%' }}
        />
        <img
          className="battery-alt"
          src={battery}
          alt="Battery"
          styles={{ width: '40%' }}
        />
        <Link to="selectnetwork">
          <img
            className="wifi-signal"
            src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi"
            styles={{ width: '40%' }}
          />
        </Link>
        <Link to="poweroff">
          <img
            className="power"
            src={power}
            alt="Power"
            styles={{ width: '40%' }}
          />
        </Link>
        <label className="barcode-alt">Uploading...</label>
        <label className="second-header">Please Wait</label>
        <div className="progress-bar">
          <span className="filled"></span>
        </div>
        <Link to="analyse">
          <img
            className="back-button"
            src={back}
            alt="Back"
            styles={{ width: '40%' }}
          />
        </Link>
        <GlobalMessage />
        {showCase3Case4Case5Modal && (
          <Popup message={uploadData.text} onClick={this.handlePopupClick} />
        )}
        {showCase1Case2Modal &&
          <Popup
            message={uploadData.text}
            subMessage="Do you wish to create new sample?"
            okLabel="Yes"
            onClick={this.handleYesClicked}
            onCancleClick={this.handlePopupCancel} />
        }
        {showCase8Modal &&
          <Case8Modal
            text={uploadData.text}
            onSave={this.handleCase8ModalSave}
            onCancel={this.handleCase8ModalCancel}
          />
        }
        {showNewSampleModal &&
          <NewSampleModal onClick={this.handleNewSampleModalSave}/>
        }
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    uploadStatus: state.analysis.upload,
    uploadData: state.analysis.upload_data,
    active_account: state.user.active_account,
    network_status: state.user.network_status,
    error: state.analysis.upload_error,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    dispatch: dispatch,
    ...bindActionCreators({ resetUploadStatus, uploadResults }, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(UploadPage);



