import React from 'react';
import {Link} from 'react-router-dom';
import { connect } from "react-redux";
import logo from '../../resources/logo.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import back from '../../resources/back.png';
import GlobalMessage from '../globalmessage/GlobalMessage';

class GetStartedPage extends React.Component {
  componentDidMount() {
    console.log(this.props);
  }
  constructor(props, context){
    super(props, context);
    this.getStarted = this.getStarted.bind(this);
  }

  getStarted(event) {
    event.preventDefault();
    this.props.history.push('nutrienttesting');
  }
  render() {
    const { network_status } = this.props;

    return (
      <div>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{width:'40%'}} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{width:'40%'}} />
          <Link to="selectnetwork"><img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi" styles={{width:'40%'}} /></Link>
          <Link to="poweroff"><img className="power" src={power} alt="Power"
            styles={{width:'40%'}} /></Link>
          <label className="get-started">Let's Get Started</label>
          <form>
            <input className="form-field-half-width first-name-placing"
              type="text" name="firstName" placeholder = "First Name" />
            <input className="form-field-half-width last-name-placing" type="text" name="lastName"
            placeholder="Last Name" />
          <input className="form-field-full-width left-placing email" type="email"
            name="emailAddress" placeholder="Email Address" />
          <input className="form-field-full-width left-placing username" type="text"
            name="userName" placeholder="User Name" />
          <input className="form-field-full-width left-placing password" type="password"
            name="password" placeholder="Password (min. 10 characters)" />
          <input className="form-field-full-width left-placing confirm-password" type="password"
            name="confirmPassword" placeholder="Confirm Password" />
            <div className="submit-container">
            <input className="submit-button-no-form-container" type="submit"
              value="Register" onClick={this.getStarted} />
            </div>
        </form>
          <Link to="firsttimeuser"><img className="back-button" src={back} alt="Back"
            styles={{width:'40%'}} /></Link>
          <GlobalMessage />
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    network_status: state.user.network_status,
  };
}

export default connect(mapStateToProps)(GetStartedPage);



