import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Link, Redirect } from "react-router-dom";
import logo from "../../resources/logo.png";
import wifi from "../../resources/wifi.png";
import wifiRed from '../../resources/wifi-red.png';
import battery from "../../resources/battery.png";
import power from "../../resources/power.png";
import back from "../../resources/back.png";
import Popup from "../popup/Popup.js";
import {
  fetchOptions,
  postOptions,
  USER_SET_OPTIONS,
} from "../../data/user/UserActions";

class UserOptionsPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      errorMessage: "",
    };

    this.saveClicked = this.saveClicked.bind(this);
    this.handlePopupClick = this.handlePopupClick.bind(this);
    this.goBack = this.goBack.bind(this);
  }

  componentDidMount() {
    const { fetchOptions } = this.props;
    fetchOptions();
  }

  async onRadioChange(id, index) {
    const { dispatch, options } = this.props;

    // Settings options in redux
    const newOption = {
      ...options[index],
      default: id,
    };
    dispatch({
      type: USER_SET_OPTIONS,
      data: [
        ...options.slice(0, index),
        newOption,
        ...options.slice(index + 1),
      ],
    });
  }

  async saveClicked(e) {
    e.preventDefault();
    const { postOptions, options } = this.props;

    // Validation
    for (const opt of options) {
      if (!opt.default) {
        this.setState((old) => ({
          ...old,
          errorMessage: opt.help_text
            ? opt.help_text
            : "Select all needed radio buttons",
        }));
        return;
      }
    }

    // Creating body for request
    let body = {}
    options.map((opt) =>
      body[opt.option.toLowerCase()] = opt.default,
    );

    const success = await postOptions(body);
    if (!success) {
      this.setState((old) => ({
        ...old,
        errorMessage:
          "Something went wrong with setting options, please try again",
      }));
      return;
    }

    this.props.history.push("nutrienttesting");
  }

  handlePopupClick() {
    this.setState((old) => ({
      ...old,
      errorMessage: "",
    }));
  }

  goBack(ev) {
    ev.preventDefault();
    this.props.history.goBack();
  }

  render() {
    const { active_account, error, network_status } = this.props;
    const { errorMessage } = this.state;

    if (error) {
      return <Redirect to='/login' />;
    }

    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img
          className="login-logo-barcode"
          src={logo}
          alt="Logo"
          styles={{ width: "40%" }}
        />
        <img
          className="battery-alt"
          src={battery}
          alt="Battery"
          styles={{ width: "40%" }}
        />
        <Link to="selectnetwork">
          <img
            className="wifi-signal"
            src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi"
            styles={{ width: "40%" }}
          />
        </Link>
        <Link to="poweroff">
          <img
            className="power"
            src={power}
            alt="Power"
            styles={{ width: "40%" }}
          />
        </Link>
        <div className={"user-options"}>
          {this.props.options.map((option, index) => {
            return (
              <div key={index} className={`white-box index-${index}`}>
                <p>{option.label}</p>
                {option.choice.map((ch) => {
                  return (
                    <div key={ch.choice_id} className="form-check">
                      <input
                        className="form-check-input"
                        type="radio"
                        id={ch.choice_id}
                        onChange={() => this.onRadioChange(ch.choice_id, index)}
                        checked={option.default === ch.choice_id}
                      />
                      <label
                        className="form-check-label"
                        htmlFor={ch.choice_id}
                      >
                        {ch.label}
                      </label>
                    </div>
                  );
                })}
              </div>
            );
          })}
          <input
            className="submit-button"
            type="submit"
            value="Save"
            onClick={(e) => this.saveClicked(e)}
          />
          <hr className="hr-login" />
        </div>
        <a onClick={this.goBack}>
          <img
            className="back-button"
            src={back}
            alt="Back"
            styles={{ width: "40%" }}
          />
        </a>
        {errorMessage && (
          <Popup message={errorMessage} onClick={this.handlePopupClick} />
        )}
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    options: state.user.options,
    error: state.user.options_error,
    options_set: state.user.options_set,
    active_account: state.user.active_account,
    network_status: state.user.network_status,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    dispatch: dispatch,
    ...bindActionCreators({ fetchOptions, postOptions }, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(UserOptionsPage);



