import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { login } from '../../data/user/UserActions.js';
import Keyboard from 'react-virtual-keyboard';
import logo from '../../resources/logo.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import back from '../../resources/back.png';
import eyeClosed from '../../resources/eye-closed.png';
import eyeOpen from '../../resources/eye-open.png';
import Popup from '../popup/Popup.js';

class LoginPage extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      username: '',
      password: '',
      modalVisible: false,
      loginError: false,
      showPassword: false,
      rememberMe: false,
    };

    let persisted_username = window.localStorage.getItem('ndt-username');
    let persisted_password = window.localStorage.getItem('ndt-password');

    if (persisted_username) {
      this.state.username = persisted_username;
      this.state.rememberMe = true;
    }

    if (persisted_password) {
      this.state.password = persisted_password;
    }

    this.usernameChanged = this.usernameChanged.bind(this);
    this.passwordChanged = this.passwordChanged.bind(this);
    this.logIn = this.logIn.bind(this);
    this.onRememberChange = this.onRememberChange.bind(this);
    this.handlePopupClick = this.handlePopupClick.bind(this);
    this.toggleShowPassword = this.toggleShowPassword.bind(this);
  }

  setPersistedUsername() {
    if (this.state.rememberMe) {
      window.localStorage.setItem("ndt-username", this.state.username);
    } else {
      window.localStorage.setItem("ndt-username", '');
    }
  }

  setPersistedPassword() {
    if (this.state.rememberMe) {
      window.localStorage.setItem("ndt-password", this.state.password);
    } else {
      window.localStorage.setItem("ndt-password", '');
    }
  }

  async usernameChanged(value) {
    await this.setState({
      username: value,
    });
    this.setPersistedUsername();
  }

  async passwordChanged(value) {
    await this.setState({
      password: value,
    });
    this.setPersistedPassword();
  }

  async onRememberChange() {
    await this.setState({
      rememberMe: !this.state.rememberMe,
    });
    this.setPersistedUsername();
    this.setPersistedPassword();
  }

  toggleShowPassword() {
    this.setState(old => ({ ...old, showPassword: !old.showPassword }))
  }

  async logIn(event) {
    event.preventDefault();

    const { login } = this.props;
    await login(this.state.username, this.state.password);

    const { currentUser } = this.props;
    this.setState({
      modalVisible: true,
      loginError: currentUser === null,
    });
  }

  handlePopupClick() {
    const { loginError } = this.state;

    this.setState({
      modalVisible: false,
    });

    if (!loginError) {
      this.props.history.push('accounts');
    }
  }

  render() {
    const { network_status } = this.props;
    const { modalVisible, loginError, showPassword } = this.state;

    //console.log('***LoginPage : this.state', this.state)
    //console.log('***LoginPage : this.state.username', this.state.username)

    return (
      <div>
        <img
          className="login-logo-barcode"
          src={logo}
          alt="Logo"
          styles={{ width: '40%' }}
        />
        <img
          className="battery-alt"
          src={battery}
          alt="Battery"
          styles={{ width: '40%' }}
        />
        <Link to="selectnetwork">
          <img
            className="wifi-signal"
            src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi"
            styles={{ width: '40%' }}
          />
        </Link>
        <Link to="poweroff">
          <img
            className="power"
            src={power}
            alt="Power"
            styles={{ width: '40%' }}
          />
        </Link>
        <form>
          <div className="form-title">Login</div>
          <Keyboard
            placeholder="Username"
            className="form-field login-username"
            value={this.state.username}
            name="username"
            options={{
              type: 'text',
              usePreview: false,
              layout: 'qwerty',
              autoAccept: true,
              alwaysOpen: false,
              appendLocally: false,
              updateOnChange: true,
              color: 'light',
              enterNavigation: true,
              keyBinding: 'touchstart',
            }}
            onChange={this.usernameChanged}
          />
          {/*<input className="form-field login-username" type="text" name="username"
          placeholder = "Username" /><br />*/}
          <div className="form-title"></div>
          <Keyboard
            placeholder="Password"
            className="form-field login-password"
            style={{ top: '193px' }}
            value={this.state.password}
            name="password"
            type={showPassword ? 'text' : 'password'}
            options={{
              type: showPassword ? 'text' : 'password',
              usePreview: false,
              layout: 'qwerty',
              autoAccept: true,
              alwaysOpen: false,
              appendLocally: false,
              updateOnChange: true,
              color: 'light',
              enterNavigation: true,
              keyBinding: 'touchstart',
            }}
            onChange={this.passwordChanged}
          />
          {showPassword && (
            <img className="password-eye-login" src={eyeOpen} alt="Eye" onClick={this.toggleShowPassword} />
          )}
          {!showPassword && (
            <img className="password-eye-login" src={eyeClosed} alt="Eye" onClick={this.toggleShowPassword}/>
          )}
          <label className="remember-me">
            <input
              type="checkbox"
              onChange={() => this.onRememberChange()}
              checked={ this.state.rememberMe }
            /> Remember me
          </label>
        {/* <a className="forgot-password" href="">
            Forgot Password?
          </a> */}

          <input
            className="submit-button"
            type="submit"
            value="Log In"
            onClick={this.logIn}
          />
          <hr className="hr-login" />
        </form>
        <Link to="selectnetwork">
          <img
            className="back-button"
            src={back}
            alt="Back"
            styles={{ width: '40%' }}
          />
        </Link>
        {modalVisible && (
          <Popup
            message={
              loginError
                ? 'Invalid username or password'
                : 'Succesfully logged in'
            }
            onClick={this.handlePopupClick}
          />
        )}
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    currentUser: state.user.current_user,
    network_status: state.user.network_status,
  };
}

export default connect(
  mapStateToProps,
  { login }
)(LoginPage);



