import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { loadWifi, connectWifi } from '../../data/user/UserActions.js';
import Keyboard from 'react-virtual-keyboard';
import logo from '../../resources/logo.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import back from '../../resources/back.png';
import eyeClosed from '../../resources/eye-closed.png';
import eyeOpen from '../../resources/eye-open.png';
import Popup from '../popup/Popup.js';

class SelectNetworkPage extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      password: '',
      modalVisible: false,
      connectionError: false,
      showPassword: false,
      selectedSSID: '',
      lastSSID: '',
      lastPassword: '',
    };

    let persistedSSID = window.localStorage.getItem('ndt-ssid');
    let persistedPassword = window.localStorage.getItem('ndt-wifi-password');

    if (persistedSSID) {
      this.state.lastSSID = persistedSSID;
      this.state.selectedSSID = persistedSSID;
    }

    if (persistedPassword) {
      this.state.password = persistedPassword;
      this.state.lastPassword = persistedPassword;
    }

    this.onConnect = this.onConnect.bind(this);
    this.passwordChanged = this.passwordChanged.bind(this);
    this.handlePopupClick = this.handlePopupClick.bind(this);
    this.goBack = this.goBack.bind(this);
    this.toggleShowPassword = this.toggleShowPassword.bind(this);
    this.onSSIDChange = this.onSSIDChange.bind(this);
  }

  setPersistedSSID() {
      window.localStorage.setItem("ndt-ssid", this.refs.ssid.value);
  }

  setPersistedPassword() {
      window.localStorage.setItem("ndt-wifi-password", this.state.password);
  }

  async onSSIDChange() {
    await this.setState({
      selectedSSID: this.refs.ssid.value,
    });
  }

  async componentDidMount() {
    const { loadWifi } = this.props;
    await loadWifi();
  }

  async onConnect(event) {
    event.preventDefault();

    const { connectWifi } = this.props;
    await connectWifi(this.refs.ssid.value, this.state.password);

    const { is_connection_error } = this.props;
    this.setState({
      modalVisible: true,
      connectionError: is_connection_error,
    });
  }

  toggleShowPassword() {
    this.setState(old => ({ ...old, showPassword: !old.showPassword }))
  }

  async passwordChanged(value) {
    await this.setState({
      password: value,
    });
  }

  handlePopupClick() {
    const { connectionError } = this.state;

    this.setState({
      modalVisible: false,
    });

    if (!connectionError) {
      //if login sccessful save the ssid and password
      this.setPersistedSSID()
      this.setPersistedPassword()
      this.props.history.push('login');
    }
  }

  goBack(ev) {
    ev.preventDefault();
    this.props.history.goBack();
  }

  render() {
    const { active_account, network_status } = this.props;
    const { modalVisible, connectionError, showPassword } = this.state;

    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img
          className="login-logo-barcode"
          src={logo}
          alt="Logo"
          styles={{ width: '40%' }}
        />
        <img
          className="battery-alt"
          src={battery}
          alt="Battery"
          styles={{ width: '40%' }}
        />
        <Link to="selectnetwork">
          <img
            className="wifi-signal"
            src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi"
            styles={{ width: '40%' }}
          />
        </Link>
        <Link to="poweroff">
          <img
            className="power"
            src={power}
            alt="Power"
            styles={{ width: '40%' }}
          />
        </Link>
        <label className="select-network" style={{ top: '120px' }}>
          Connect To Wi-Fi
        </label>
        <form>
          <div className="select" style={{ top: '188px' }}>
            <select ref="ssid"
              onChange={() => this.onSSIDChange()}
              value={this.state.selectedSSID}>
              {this.props.wifi_ssid_list.map(w => (
                <option key={w} value={w}>
                  {w}
                </option>
              ))}
            </select>
            <div className="select_arrow"></div>
          </div>
          <Keyboard
            placeholder='Password'
            className="form-field-network-password"
            style={{ top: '188px' }}
            value={this.state.lastPassword}
            name="password"
            type={showPassword ? 'text' : 'password'}
            options={{
              type: showPassword ? 'text' : 'password',
              usePreview: false,
              layout: 'qwerty',
              autoAccept: true,
              alwaysOpen: false,
              appendLocally: false,
              updateOnChange: true,
              color: 'light',
              enterNavigation: true,
              keyBinding: 'touchstart',
            }}
            onChange={(e)=>{this.passwordChanged(e)}}
          />
          {showPassword && (
            <img className="password-eye-network" src={eyeOpen} alt="Eye" onClick={this.toggleShowPassword} />
          )}
          {!showPassword && (
            <img className="password-eye-network" src={eyeClosed} alt="Eye" onClick={this.toggleShowPassword}/>
          )}
          <input
            className="network-connect"
            type="submit"
            value="Connect"
            onClick={this.onConnect}
          />
        </form>
        <a onClick={this.goBack}>
          <img
            className="back-button"
            src={back}
            alt="Back"
            styles={{ width: '40%' }}
          />
        </a>
        {modalVisible && (
          <Popup
            message={
              connectionError
                ? 'Invalid Wi-Fi password'
                : 'Succesfully connected'
            }
            onClick={this.handlePopupClick}
          />
        )}
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    wifi_ssid_list: state.user.wifi_ssid_list,
    is_connection_error: state.user.is_connection_error,
    active_account: state.user.active_account,
    network_status: state.user.network_status,
  };
}

export default connect(
  mapStateToProps,
  { loadWifi, connectWifi }
)(SelectNetworkPage);



