import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { poweroff } from '../../data/analysis/AnalysisActions.js';
import logo from '../../resources/logo.png';
import wifi from '../../resources/wifi.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import back from '../../resources/back.png'
import GlobalMessage from '../globalmessage/GlobalMessage.js';

class PowerOffPage extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.powerOff = this.powerOff.bind(this);
    this.backClick = this.backClick.bind(this);
  }

  powerOff(event) {
    event.preventDefault();

    const { poweroff } = this.props;
    poweroff();

    this.props.history.push('thanks');
  }

  backClick() {
    this.props.history.goBack();
  }

  render() {
    return (
      <div>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{ width: '40%' }} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{ width: '40%' }} />
        <Link to="selectnetwork"><img className="wifi-signal" src={wifi}
          alt="Wifi" styles={{ width: '40%' }} /></Link>
        <Link to="poweroff"><img className="power" src={power} alt="Power"
          styles={{ width: '40%' }} /></Link>
        <label className="power-off">Do you want to shutdown the device?</label>
        <input className="power-on-button" type="button" value="Yes" onClick={this.powerOff} />
        <input onClick={this.backClick} className="power-off-button" type="button" value="No" />
        <a onClick={this.backClick}><img className="back-button" src={back} alt="Back"
          styles={{ width: '40%' }} /></a>
        <GlobalMessage />
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    state
  };
}

export default connect(mapStateToProps, { poweroff })(PowerOffPage);



