import * as axios from 'axios';

export const ANALYSIS_START = "/analysis/start";
export const ANALYSIS_BARCODE = "/analysis/barcode";
export const ANALYSIS_FINISH = "/analysis/finish";
export const ANALYSIS_STORED_RESULTS = "/analysis/stored_results";
export const ANALYSIS_STORED_RESULTS_ERROR = "/analysis/stored_results_error";
export const ANALYSIS_UPLOAD = "/analysis/upload";
export const ANALYSIS_UPLOAD_ERROR = "/analysis/upload_error";

export function startAnalysis() {
    return function (dispatch) {
        return axios.post('/api/analysis/start')
            .then((response) => {
                return dispatch({
                    type: ANALYSIS_START
                })
            })
            .catch(error => console.log(error));
    }
}

export function checkBarcode() {
    return function (dispatch) {
        return axios.get('/api/analysis/barcode')
            .then((response) => {
                return dispatch({
                    type: ANALYSIS_BARCODE,
                    status: true
                })
            })
            .catch((error) => {
                return dispatch({
                    type: ANALYSIS_BARCODE,
                    status: false
                })
            });
    }
}

export function checkAnalysisFinished() {
    return function (dispatch) {
        return axios.get('/api/analysis/status')
            .then((response) => {
                return dispatch({
                    type: ANALYSIS_FINISH,
                    data: response.data
                })
            })
            .catch((error) => {
                console.log("analysis not finished")
            });
    }
}

export function getStoredResults() {
    return function (dispatch) {
        return axios.get('/api/analysis/stored_results')
            .then((response) => {
                return dispatch({
                    type: ANALYSIS_STORED_RESULTS,
                    data: response.data
                })
            })
            .catch((error) => {
                console.log("error fetching stored results")
                console.log(error);
                return dispatch({
                  type: ANALYSIS_STORED_RESULTS_ERROR,
                  data: error.response.data // This should save the response data of the server
                })
            });
    }
}

export function poweroff() {
    return function (dispatch) {
        return axios.post('/api/poweroff');
    }
}

export function resetUploadStatus() {
    return ({
        type: ANALYSIS_UPLOAD,
        payload: {
            status: false,
            upload_data: {}
        },
    })
}

export function uploadResults(body) {
    var data = new FormData();
    if (body.new_sample !== undefined) {
        data.append("new_sample", body.new_sample);
    }
    if (body.sample_id !== undefined) {
        data.append("sample_id", body.sample_id);
    }
    if (body.store_local !== undefined) {
        data.append("store_local", body.store_local);
    }
    if (body.local_id !== undefined) {
        data.append("local_id", body.local_id);
    }

    return function (dispatch) {
        return axios.post('/api/analysis/upload', data)
        .then((response) => {
            //console.log('****UPLOAD', JSON.stringify(response));
            return dispatch({
                type: ANALYSIS_UPLOAD,
                payload: {
                    status: true,
                    upload_data: response.data
                }
            })
        })
        .catch((error) => {
            console.log("error uploading results");
            let dispatchData = 'An error occurred';
            if (error.response) {
              dispatchData = error.response.data;
            }
            return dispatch({
              type: ANALYSIS_UPLOAD_ERROR,
              data: dispatchData // This should save the response data of the server
            })
        });
    }
}



