import React from "react";

export default class Popup extends React.Component {
  handleOnClick = () => {
    const { onClick } = this.props;
    if (onClick) {
      onClick();
    }
  };

  cancelClicked = () => {
    const { onCancleClick } = this.props;
    if (onCancleClick) {
      onCancleClick();
    }
  }

  render() {
    const { message, subMessage, onCancleClick, okLabel, inputComponent } = this.props;
    const secondButtonEnabled = onCancleClick ? true : false;

    return (
      <div className="popup-background">
        <div className="popup-container" style={inputComponent ? { top: '60px', height: '250px' } : {}}>
          <div className="popup-message-container">
            <div className="popup-message">
              {message}
              <br />
              {subMessage}
            </div>
          </div>
          {inputComponent &&
            inputComponent
          }
          <input
            className="popup-button"
            style={secondButtonEnabled ? { left: '30px' } : {}}
            type="submit"
            value={okLabel ? okLabel : "OK"}
            onClick={this.handleOnClick}
          />
          {secondButtonEnabled && (
            <input
              className="popup-button"
              style={{ left: '170px' }}
              type="submit"
              value="Cancel"
              onClick={this.cancelClicked}
            />
          )}
        </div>
      </div>
    );
  }
}



