import React from 'react';
import { connect } from 'react-redux';
import { checkBarcode } from '../../data/analysis/AnalysisActions.js';
import { Link } from 'react-router-dom';
import logo from '../../resources/logo.png';
import back from '../../resources/back.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';

class BarCodePage extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      timer: null
    }

    this.timerTick = this.timerTick.bind(this);
    this.skip = this.skip.bind(this);
  }

  componentDidMount() {
    if (this.props.barcode_scanned) {
      this.props.history.push('analysis');
      return;
    }

    let timer = setInterval(this.timerTick, 1000);

    this.setState({
      timer: timer
    })
  }

  componentWillUnmount() {
    clearInterval(this.state.timer);
  }

  timerTick() {
    console.log("checking for barcode")
    const { checkBarcode } = this.props;
    checkBarcode();
  }

  componentWillReceiveProps(newProps) {
    if (newProps.barcode_scanned) {
      this.props.history.push('analysis');
    }
  }

  skip() {
    this.props.history.push('analysis');
  }

  render() {
    const { active_account, network_status } = this.props;

    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{ width: '40%' }} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{ width: '40%' }} />
        <Link to="selectnetwork"><img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
          alt="Wifi" styles={{ width: '40%' }} /></Link>
        <Link to="poweroff"><img className="power" src={power} alt="Power"
          styles={{ width: '40%' }} /></Link>
        <h2 className="barcode">Scan Barcode To Begin Analysis</h2>
        {/* <input className="square-button" type="button" value="Analyze" onClick={this.analyse} /> */}
        <Link to="analyse"><img className="back-button" src={back} alt="Back"
          styles={{ width: '40%' }} /></Link>
        <input className="square-button barcode-skip" type="button" value="Skip" onClick={this.skip} />
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    barcode_scanned: state.analysis.barcode,
    active_account: state.user.active_account,
    network_status: state.user.network_status,
  };
}

export default connect(mapStateToProps, { checkBarcode })(BarCodePage);



