import React from "react";
import Keyboard from "react-virtual-keyboard";

export default class NewSampleModal extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      showInput: false,
      inputValue: "",
      radioValue: "barcode",
    };

    this.requestSampleChanged = this.requestSampleChanged.bind(this);
    this.onRadioChange = this.onRadioChange.bind(this);
  }

  handleOnClick = () => {
    const { onClick } = this.props;
    if (onClick) {
      onClick(this.state);
    }
  };

  onRadioChange(value) {
    this.setState((old) => ({
      ...old,
      radioValue: value,
    }));
  }

  requestSampleChanged(value) {
    this.setState((old) => ({
      ...old,
      inputValue: value,
    }));
  }

  render() {
    const { inputValue, radioValue } = this.state;

    return (
      <div className="popup-background">
        <div
          className="popup-container"
          style={{ height: "250px", top: "30px" }}
        >
          <div className="popup-message-container" style={{ height: "100px" }}>
            <div className="popup-message">Create new sample:</div>
          </div>
          <div className="modal-radio">
            <div className="form-check">
              <input
                className="form-check-input"
                type="radio"
                onChange={() => this.onRadioChange("barcode")}
                checked={radioValue === "barcode"}
              />
              <label className="form-check-label">
                Make the sample_id = the barcode
              </label>
            </div>
            <div className="form-check">
              <input
                className="form-check-input"
                type="radio"
                onChange={() => this.onRadioChange("ownSample")}
                checked={radioValue === "ownSample"}
              />
              <label className="form-check-label">
                Type in your own sample_id
              </label>
            </div>
          </div>
          {radioValue === "ownSample" && (
            <Keyboard
              style={{ top: "135px" }}
              placeholder="Enter your sample_id here"
              className="form-field request-sample"
              value={inputValue}
              name="request-sample"
              options={{
                type: "text",
                usePreview: false,
                layout: "qwerty",
                autoAccept: true,
                alwaysOpen: false,
                appendLocally: false,
                updateOnChange: true,
                color: "light",
                enterNavigation: true,
                keyBinding: 'touchstart',
              }}
              onChange={this.requestSampleChanged}
            />
          )}
          <input
            className="popup-button"
            type="submit"
            value="Save"
            onClick={this.handleOnClick}
          />
        </div>
      </div>
    );
  }
}



