import React from 'react';
import {Link} from 'react-router-dom';
import { connect } from "react-redux";
import logo from '../../resources/logo.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import analysis from '../../resources/analysis.png';
import statistics from '../../resources/statistics.png';
import settings from '../../resources/settings.png';
import power from '../../resources/power.png';
import GlobalMessage from '../globalmessage/GlobalMessage';

class NutrientTestingPage extends React.Component {
  componentDidMount() {
    console.log(this.props);
  }

  render() {
    const { active_account, network_status } = this.props;

    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{width:'40%'}} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{width:'40%'}} />
          <Link to="selectnetwork"><img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi" styles={{width:'40%'}} /></Link>
          <Link to="poweroff"><img className="power" src={power} alt="Power"
            styles={{width:'40%'}} /></Link>
        <label className="nutrient-testing">Soil Nutrient Testing</label>

            <Link to="analyse"><img src={analysis} alt="Analyse"
            styles={{width:'40%'}} className="analyse-icon" /></Link>
          <span className="analyse-caption">Analyse</span>

            <Link to="statistics"><img src={statistics} alt="Statistics"
            styles={{width:'40%'}} className="statistics-icon" /></Link>
          <span className="statistics-caption">Stored Results</span>

            <Link to="settings"><img src={settings} alt="Settings"
            styles={{width:'40%'}} className="settings-icon" /></Link>
          <span className="settings-caption">Settings</span>

        <GlobalMessage />
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    active_account: state.user.active_account,
    network_status: state.user.network_status,
  };
}

export default connect(mapStateToProps)(NutrientTestingPage);



