import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { getStoredResults, uploadResults } from '../../data/analysis/AnalysisActions.js';
import { getUserState } from "../../data/user/UserActions";
import Popup from '../popup/Popup.js';
import logo from '../../resources/logo.png';
import back from '../../resources/back.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import calendar from '../../resources/calendar.png';
import K from '../../resources/K.png';
import P from '../../resources/P.png';
import NH4 from '../../resources/NH4.png';
import No3 from '../../resources/NO3_Up.png';
import tick from '../../resources/tick.png';
import cross from '../../resources/cross.png';
import GlobalMessage from '../globalmessage/GlobalMessage';
import Case8Modal from '../upload/Case8Modal';
import NewSampleModal from '../upload/NewSampleModal';

class StatisticsPage extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      showNewSampleModal: false,
      showCase1Case2Modal: false,
      showCase3Case4Case5Modal: false,
      showCase8Modal: false,
      chosenLocalId: null,
    }

    this.renderResult = this.renderResult.bind(this);
    this.case3Case4PopupClick = this.case3Case4PopupClick.bind(this);
    this.case1Case2YesClicked = this.case1Case2YesClicked.bind(this);
    this.case1Case2Cancel = this.case1Case2Cancel.bind(this);
    this.handleNewSampleModalSave = this.handleNewSampleModalSave.bind(this);
    this.handleCase8ModalSave = this.handleCase8ModalSave.bind(this);
    this.handleCase8ModalCancel = this.handleCase8ModalCancel.bind(this);
  }

  componentDidMount() {
    const { getStoredResults } = this.props;
    getStoredResults();
  }

  async crossClicked(ev, local_id) {
    ev.preventDefault();
    const { uploadResults } = this.props;
    await uploadResults({
      local_id: local_id
    });
    const { uploadData } = this.props;
    this.setState(old => ({
      ...old,
      showCase1Case2Modal: uploadData.case === 'case1' || uploadData.case === 'case2',
      showCase3Case4Case5Modal: uploadData.case === 'case3' || uploadData.case === 'case4' || uploadData.case === 'case5',
      chosenLocalId: local_id
    }));
  }

  async case1Case2YesClicked() {
    const { uploadData, uploadResults, getStoredResults, getUserState } = this.props;
    const { chosenLocalId } = this.state;
    if (uploadData.case === 'case1') {
      this.setState(old => ({
        ...old,
        showNewSampleModal: true,
        showCase1Case2Modal: false,
      }))
      return;
    }

    await uploadResults({
      new_sample: true,
      sample_id: uploadData.sample_id,
      local_id: chosenLocalId,
    });

    await getStoredResults();
    await getUserState();

    this.setState(old => ({
      ...old,
      showCase1Case2Modal: false,
    }));
  }

  async case1Case2Cancel() {
    const { uploadData, uploadResults, getStoredResults, getUserState } = this.props;
    const { chosenLocalId } = this.state;

    if (uploadData.sample_id) {
      await uploadResults({
        new_sample: false,
        sample_id: uploadData.sample_id,
        store_local: true,
        local_id: chosenLocalId,
      });
    } else {
      await uploadResults({
        new_sample: false,
        store_local: true,
        local_id: chosenLocalId,
      });
    }

    // await uploadResults({
    //   new_sample: false,
    //   sample_id: uploadData.sample_id ? uploadData.sample_id : null,
    //   store_local: true,
    //   local_id: chosenLocalId,
    // });

    await getStoredResults();
    await getUserState();

    this.setState(old => ({
      ...old,
      showCase1Case2Modal: false,
    }));
  }

  async case3Case4PopupClick() {
    const { getStoredResults, getUserState } = this.props;
    await getStoredResults();
    await getUserState();

    this.setState(old => ({
      ...old,
      showCase3Case4Case5Modal: false,
    }));
  }

  async handleNewSampleModalSave(data) {
    const { uploadResults } = this.props;
    const { chosenLocalId } = this.state;

    if (data.radioValue === 'barcode') {
      await uploadResults({
        new_sample: true,
        local_id: chosenLocalId,
      });
      this.setState(old => ({
        ...old,
        showNewSampleModal: false,
        showCase3Case4Case5Modal: true
       }));
      return;
    }

    await uploadResults({
      new_sample: true,
      sample_id: data.inputValue,
      local_id: chosenLocalId,
    });

    // If the response of upload is case 8
    const { uploadData } = this.props;
    if (uploadData.case === 'case8') {
      this.setState(old => ({
        ...old,
        showCase8Modal: true,
        showCase3Case4Case5Modal: false,
        showCase1Case2Modal: false,
        showNewSampleModal: false,
      }));
    }

    // If the response of upload is case 5
    if (uploadData.case === 'case5') {
      this.setState(old => ({
        ...old,
        showCase8Modal: false,
        showCase3Case4Case5Modal: true,
        showCase1Case2Modal: false,
        showNewSampleModal: false,
      }));
    }
  }

  async handleCase8ModalSave(sample_id) {
    const { uploadResults, getStoredResults, getUserState } = this.props;
    const { chosenLocalId } = this.state;
    await uploadResults({
      new_sample: true,
      sample_id,
      local_id: chosenLocalId,
    });

    // If the response of upload is case 8
    const { uploadData } = this.props;
    if (uploadData.case === 'case8') {
      this.setState(old => ({
        ...old,
        showCase8Modal: true,
        showCase3Case4Case5Modal: false,
        showCase1Case2Modal: false,
        showNewSampleModal: false,
      }));
      return;
    }

    await getStoredResults();
    await getUserState();

    this.setState(old => ({
      ...old,
      showCase8Modal: false,
    }));

  }

  async handleCase8ModalCancel() {
    const { uploadResults, getStoredResults, getUserState } = this.props;
    const { chosenLocalId } = this.state;

    await uploadResults({
      new_sample: true,
      store_local: true,
      local_id: chosenLocalId,
    });

    await getStoredResults();
    await getUserState();

    this.setState(old => ({
      ...old,
      showCase8Modal: false,
    }));
  }

  renderResult(result) {
    return (
      <div key={result.timestamp} className="statistics-row">
        <div className="statistics-row-header" style={{minHeight: '40px'}}>
          {result.data_id ? result.data_id : '- No data id -'} <br />
          {result.timestamp}
        </div>
        <div className="statistics-row-header" style={{marginTop: "-5%", marginLeft: '47%'}}>
          {result.sample_id}
        </div>
        <div className="statistics-row-header" style={{marginTop: "-3%", marginLeft: '68%'}}>
          {result.account_name}
        </div>
        <div className="statistics-horizontal-container">
          <div className="no3-horizontal-item">
            <img src={No3} alt="NO3" styles={{ width: '40%' }} />
            <span className="no3-caption">{result.data.N1.toFixed(0)} ppm</span>
          </div>
          <div className="no2-horizontal-item">
            <img src={NH4} alt="NH4" styles={{ width: '40%' }} />
            <span className="no2-caption">{result.data.N2.toFixed(0)} ppm</span>
          </div>
          <div className="p-horizontal-item">
            <img src={P} alt="P" styles={{ width: '40%' }} />
            <span className="p-caption">{result.data.P.toFixed(0)} ppm</span>
          </div>
          <div className="tick-horizontal-item">
            {result.uploaded === true &&
              <img src={tick} alt="tick" styles={{ width: '40%' }} />
            }
            {result.uploaded === false &&
            // eslint-disable-next-line
            <a href="#" onClick={(ev) => this.crossClicked(ev, result.local_id)}>
              <img src={cross} alt="cross" styles={{ width: '40%' }} />
            </a>
            }
          </div>
          <div className="k-horizontal-item">
            <img src={K} alt="K" styles={{ width: '40%' }} />
            <span className="k-caption">{result.data.K.toFixed(0)} ppm</span>
          </div>
        </div>
      </div>
    )
  }

  render() {
    const {
      uploadData,
      active_account,
      network_status
    } = this.props;

    const {
      showNewSampleModal,
      showCase1Case2Modal,
      showCase3Case4Case5Modal,
      showCase8Modal
    } = this.state;

    let startDate = "-"
    let endDate = "-"

    if(this.props.stored_results.length > 0) {
      startDate = this.props.stored_results[0].timestamp.substring(0, 10);
      endDate = this.props.stored_results[this.props.stored_results.length - 1].timestamp.substring(0, 10);
    }

    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{ width: '40%' }} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{ width: '40%' }} />
        <Link to="selectnetwork"><img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
          alt="Wifi" styles={{ width: '40%' }} /></Link>
        <Link to="poweroff"><img className="power" src={power} alt="Power"
          styles={{ width: '40%' }} /></Link>
        <div className="statistics-header">
          <label className="statistics-header-title">Starting Date</label>
          <label className="statistics-header-date">{startDate}</label>
          <img className="calendar-start" src={calendar} alt="Calendar" styles={{ width: '40%' }} />
          <label className="statistics-header-title">Ending Date</label>
          <label className="statistics-header-date">{endDate}</label>
          <img className="calendar-end" src={calendar} alt="Calendar" styles={{ width: '40%' }} />
        </div>
        <div className="statistics-section">
          {this.props.stored_results.map(r => this.renderResult(r))}
        </div>
        <Link to="nutrienttesting"><img className="back-button" src={back} alt="Back"
          styles={{ width: '40%' }} /></Link>
        <GlobalMessage customMessage={'You have analyses that are not uploaded to the database.'} />
        {showCase3Case4Case5Modal && (
          <Popup message={uploadData.text} onClick={this.case3Case4PopupClick} />
        )}
        {showCase1Case2Modal &&
          <Popup
            message={uploadData.text}
            subMessage="Do you wish to create new sample?"
            okLabel="Yes"
            onClick={this.case1Case2YesClicked}
            onCancleClick={this.case1Case2Cancel} />
        }
        {showCase8Modal &&
          <Case8Modal
            text={uploadData.text}
            onSave={this.handleCase8ModalSave}
            onCancel={this.handleCase8ModalCancel}
          />
        }
        {showNewSampleModal &&
          <NewSampleModal onClick={this.handleNewSampleModalSave}/>
        }
      </div>
    );
  }
}


function mapStateToProps(state, ownProps) {
  return {
    stored_results: state.analysis.stored_results,
    uploadData: state.analysis.upload_data,
    active_account: state.user.active_account,
    network_status: state.user.network_status,
  };
}

export default connect(mapStateToProps, { getStoredResults, uploadResults, getUserState })(StatisticsPage);



