import React from 'react';
import {Link} from 'react-router-dom';
import { connect } from 'react-redux';
import { getUserState } from '../../data/user/UserActions.js';
import logo from '../../resources/logo.png';
import back from '../../resources/back.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import GlobalMessage from '../globalmessage/GlobalMessage';

class SettingsPage extends React.Component {
  // componentDidMount() {
  //   console.log(this.props);
  // }

  constructor(props, context){
    super(props, context);

    this.brightness = this.brightness.bind(this);
    this.version = this.version.bind(this);
    this.selectNetwork = this.selectNetwork.bind(this);
    this.selectAccount = this.selectAccount.bind(this);
    this.userOptions = this.userOptions.bind(this);
    this.logout = this.logout.bind(this);
  }

  async componentDidMount() {
    const { getUserState } = this.props;
    console.log('***in componentDidMount', this.props);

    if (this.props.user_state.length === 0) {
      await getUserState();
    }

    // const option = this.props.options.find(o => o.option === 'Use_barcode');
    // if (option && option.default === 2) {
    //   this.setState(old => ({ ...old, should_show_barcode_modal: true }));
    // }
  }

  brightness(event) {
    event.preventDefault();
    this.props.history.push('brightness');
  }
  version(event) {
    event.preventDefault();
    this.props.history.push('version');
  }
  selectNetwork(event) {
    event.preventDefault();
    this.props.history.push('selectnetwork');
  }
  selectAccount(event) {
    event.preventDefault();
    this.props.history.push('accounts');
  }
  userOptions(event) {
    event.preventDefault();
    this.props.history.push('user-options');
  }
  logout(event) {
    event.preventDefault();
    this.props.history.push('login');
  }

  render() {
    const { active_account, network_status } = this.props;
      console.log('****render this.props', this.props);


    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{width:'40%'}} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{width:'40%'}} />
          <Link to="selectnetwork"><img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi" styles={{width:'40%'}} /></Link>
          <Link to="poweroff"><img className="power" src={power} alt="Power"
            styles={{width:'40%'}} /></Link>
        <div className="settings-section" style={{marginTop: '10%'}}>
          <input className="settings-button right" type="button"
            value="Accounts" onClick={this.selectAccount}/>
          <input className="settings-button" type="button" value="User Options"
            onClick={this.userOptions} />
        </div>
        <div className="settings-section">
          <input className="settings-button right" type="button" value="Network"
            onClick={this.selectNetwork} />
          <input className="settings-button" type="button" value="Logout" onClick={this.logout} />
        </div>
        <div className={`settings-box`}>
           <div class="row">
            <div class="col text-center">CURRENT SETTINGS</div>
           </div>
           <div class="row">
            <div class="col-2">User:</div>
            <div class="col-4">{this.props.user_state.current_user}</div>
            <div class="col-4">Analysis type:</div>
            <div class="col-2">{this.props.user_state.user_options_model_type_t}</div>
           </div>
           <div class="row">
            <div class="col-2">Account:</div>
            <div class="col-4">{this.props.user_state.active_account_name}</div>
            <div class="col-4">Nitrate model:</div>
            <div class="col-2">{this.props.user_state.nitrate_model}</div>
           </div>
           <div class="row">
            <div class="col-2">Barcode:</div>
            <div class="col-4">{this.props.user_state.user_options_use_barcode_t}</div>
            <div class="col-4">Phosphate model:</div>
            <div class="col-2">{this.props.user_state.phosphate_model}</div>
           </div>
        </div>
        <Link to="nutrienttesting"><img className="back-button" src={back} alt="Back"
          styles={{width:'40%'}} /></Link>
        <GlobalMessage />
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    active_account: state.user.active_account,
    network_status: state.user.network_status,
    user_state: state.user.user_state,
  };
}

export default connect(mapStateToProps, { getUserState })(SettingsPage);;



