import React from 'react';
import {Link} from 'react-router-dom';
import { connect } from "react-redux";
import logo from '../../resources/logo.png';
import user from '../../resources/user.png';
import visitor from '../../resources/repeat_visitor.png';
import manual from '../../resources/user_manual.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import back from '../../resources/back.png';

class FirstTimeUserPage extends React.Component {
  componentDidMount() {
    console.log(this.props);
  }
  constructor(props, context){
    super(props, context);

    this.goToLogin = this.goToLogin.bind(this);
    this.goToManual = this.goToManual.bind(this);
    this.goToStarted = this.goToStarted.bind(this);
  }

  goToLogin(event) {
    event.preventDefault();
    this.props.history.push('login');
  }
  goToManual(event) {
    event.preventDefault();
    this.props.history.push('manual');
  }
  goToStarted(event){
    event.preventDefault();
    this.props.history.push('getStarted');
  }
  render() {
    const { network_status } = this.props;

    return (
      <div>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{width:'40%'}} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{width:'40%'}} />
          <Link to="selectnetwork"><img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi" styles={{width:'40%'}} /></Link>
          <Link to="poweroff"><img className="power" src={power} alt="Power"
            styles={{width:'40%'}} /></Link>

          <img className="first-time-user-icon" src={user} alt="First Time User" styles={{width:'40%'}} />
          <br></br>
          <input className="vertical-form-button first-time-user-button" type="button"
            value="First Time User" onClick={this.goToStarted} />

          <img className="repeat-user-icon" src={visitor} alt="Repeat Visitor" styles={{width:'40%'}} />
          <br></br>
          <input className="vertical-form-button repeat-user-button" type="button" value="Repeat Visitor"
            onClick={this.goToLogin}/>

          <img className="manual-icon" src={manual} alt="User Manual" styles={{width:'40%'}} />
          <br></br>
          <input className="vertical-form-button long-text-font manual-button" type="button"
            value="Click To Browse The User Manual" onClick={this.goToManual}/>

          <Link to="selectnetwork"><img className="back-button" src={back} alt="Back"
          styles={{width:'40%'}} /></Link>
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    network_status: state.user.network_status,
  };
}

export default connect(mapStateToProps)(FirstTimeUserPage);



