import * as AnalysisActions from './AnalysisActions.js';

export const initialState = {
    started: false,
    barcode: false,
    finished: false,
    result: null,
    stored_results: [],
    upload: false,
    upload_data: {}
}

export default function user(state = initialState, action) {
    switch (action.type) {
        case 'UNAUTHORIZED':
            return initialState
        case AnalysisActions.ANALYSIS_START:
            return {
                ...initialState,
                started: true
            }
        case AnalysisActions.ANALYSIS_BARCODE:
            return {
                ...state,
                barcode: action.status
            }
        case AnalysisActions.ANALYSIS_FINISH:
            return {
                ...state,
                finished: true,
                result: action.data
            }
        case AnalysisActions.ANALYSIS_STORED_RESULTS:
            return {
                ...state,
                stored_results: action.data,
                stored_results_error: null,
            }
        case AnalysisActions.ANALYSIS_STORED_RESULTS_ERROR:
            return {
                ...state,
                stored_results_error: action.data
            }
        case AnalysisActions.ANALYSIS_UPLOAD:
            return {
                ...state,
                upload: action.payload.status,
                upload_data: action.payload.upload_data,
                // reset analysis daya when uploaded
                finished: false,
                result: null,
                upload_error: null,
            }
        case AnalysisActions.ANALYSIS_UPLOAD_ERROR:
            return {
                ...state,
                //upload: action.payload.status,
                //upload_data: action.payload.upload_data,
                upload_error: action.data,
                // reset analysis daya when uploaded
                finished: false,
                result: null,
            }
        default:
            return state;
    }
}



