import * as UserActions from "./UserActions.js";

export const initialState = {
  current_user: null,
  login_error_message: null,
  is_logging_in: false,
  wifi_ssid_list: [],
  wifi_ssid_connected: null,
  is_connection_error: false,
  accounts: [],
  options: [],
  active_account: false,
  options_set: false,
  user_state: {}
};

export default function user(state = initialState, action) {
  switch (action.type) {
    case "UNAUTHORIZED":
      return initialState;
    case UserActions.USER_LOGIN:
      return {
        ...state,
        current_user: action.data,
        login_error_message: null,
      };
    case UserActions.USER_LOGIN_ERROR:
      return {
        ...state,
        current_user: null,
        login_error_message: action.data,
      };
    case UserActions.USER_LOAD_WIFI:
      return {
        ...state,
        wifi_ssid_list: action.data,
      };
    case UserActions.USER_CONNECT_WIFI:
      return {
        ...state,
        wifi_ssid_connected: action.ssid,
        is_connection_error: false,
      };
    case UserActions.USER_CONNECT_WIFI_ERROR:
      return {
        ...state,
        is_connection_error: true,
      };
    case UserActions.USER_GET_ACCOUNTS:
      return {
        ...state,
        accounts: action.data,
      };
    case UserActions.USER_SET_ACCOUNT:
      return {
        ...state,
        active_account: action.data,
      };
    case UserActions.USER_SET_OPTIONS:
      return {
        ...state,
        options: action.data,
        options_error: null,
      };
    case UserActions.USER_SET_OPTIONS_ERROR:
      return {
        ...state,
        options_error: action.data,
      };
    case UserActions.USER_SET_STATE:
      return {
        ...state,
        user_state: action.data
      }
    case UserActions.USER_GET_NETWORK_STATUS:
      return {
        ...state,
        network_status: action.data.network_status,
      }
    default:
      return state;
  }
}



