import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { checkAnalysisFinished } from '../../data/analysis/AnalysisActions';
import { getUserState } from "../../data/user/UserActions";
import logo from '../../resources/logo.png';
// import back from '../../resources/back.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import Popup from '../popup/Popup.js';
import GlobalMessage from '../globalmessage/GlobalMessage.js';

class AnalysisPage extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      seconds_total: 0,
      seconds_elapsed: 0,
      percentage_complete: 0,
    };

    this.timerTick = this.timerTick.bind(this);
    this.handlePopupClick = this.handlePopupClick.bind(this);
  }

  async componentDidMount() {
    if (this.props.barcode_scanned) {
      this.props.history.push('analyse');
      return;
    }

    const { getUserState } = this.props;
    await getUserState();

    let timer = setInterval(this.timerTick, 1000);

    const { userState } = this.props;
    this.setState({
      timer: timer,
      seconds_total: userState.analysis_time
    });
  }

  componentWillUnmount() {
    clearInterval(this.state.timer);
  }

  timerTick() {
    let seconds_elapsed = this.state.seconds_elapsed + 1;
    let percentage_complete =
      (seconds_elapsed / this.state.seconds_total) * 100;

    if (percentage_complete > 100) percentage_complete = 100;

    this.setState({
      seconds_elapsed,
      percentage_complete,
    });

    const { checkAnalysisFinished } = this.props;
    checkAnalysisFinished();
  }

  handlePopupClick() {
    this.props.history.push('analyse');
  }

  render() {
    const { analysisFinished, active_account, network_status } = this.props;

    return (
      <div>
        <h4 className="account-title">{active_account.name}</h4>
        <img
          className="login-logo-barcode"
          src={logo}
          alt="Logo"
          styles={{ width: '40%' }}
        />
        <img
          className="battery-alt"
          src={battery}
          alt="Battery"
          styles={{ width: '40%' }}
        />
        <Link to="selectnetwork">
          <img
            className="wifi-signal"
            src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi"
            styles={{ width: '40%' }}
          />
        </Link>
        <Link to="poweroff">
          <img
            className="power"
            src={power}
            alt="Power"
            styles={{ width: '40%' }}
          />
        </Link>
        <label className="barcode-alt">Analysing...</label>
        <label className="second-header">Please Wait</label>
        <div className="progress-bar">
          <span
            className="filled"
            style={{ width: this.state.percentage_complete + '%' }}
          ></span>
        </div>
        {/* <Link to="barcode">
          <img
            className="back-button"
            src={back}
            alt="Back"
            styles={{ width: '40%' }}
          />
        </Link>   */}

        <GlobalMessage />

        {analysisFinished && (
          <Popup
            message={'Analysis complete'}
            onClick={this.handlePopupClick}
          />
        )}
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    analysisFinished: state.analysis.finished,
    active_account: state.user.active_account,
    userState: state.user.user_state,
    network_status: state.user.network_status,
  };
}

export default connect(
  mapStateToProps,
  { checkAnalysisFinished, getUserState }
)(AnalysisPage);



