import React from "react";
import { connect } from "react-redux";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import * as axios from "axios";
import { getNetworkStatus } from './data/user/UserActions';
import WelcomePage from "./components/welcome/WelcomePage";
import LoginPage from "./components/login/LoginPage";
import AccountsPage from "./components/accounts/Accounts";
import ManualPage from "./components/manual/ManualPage";
import ThankYouPage from "./components/thankyou/ThankYouPage";
import PowerOffPage from "./components/poweroff/PowerOffPage";
import GetStartedPage from "./components/getstarted/GetStartedPage";
import FirstTimeUserPage from "./components/firsttimeuser/FirstTimeUserPage";
import NutrientTestingPage from "./components/nutrienttesting/NutrientTestingPage";
import SelectNetworkPage from "./components/selectnetwork/SelectNetworkPage";
import BarCodePage from "./components/barcode/BarCodePage";
import AnalysisPage from "./components/analysis/AnalysisPage";
import AnalysePage from "./components/analysis/AnalysePage";
import UploadPage from "./components/upload/UploadPage";
import CalibratePage from "./components/calibrate/CalibratePage";
import SettingsPage from "./components/settings/SettingsPage";
import VersionPage from "./components/version/VersionPage";
import BrightnessPage from "./components/brightness/BrightnessPage";
import StatisticsPage from "./components/statistics/StatisticsPage";
import GuidePage from "./components/guides/GuidePage";
import UserOptions from "./components/useroptions/UserOptions";

axios.defaults.baseURL = "http://127.0.0.1:5000";

class App extends React.Component {
  componentDidMount() {
    const { getNetworkStatus } = this.props;
    getNetworkStatus();
    setInterval(getNetworkStatus, 30000);
  }

  render() {
    return (
        <div className="container-fluid">
          <BrowserRouter>
            <Switch>
              <Route exact path="/" component={WelcomePage} />
              <Route path="/login" component={LoginPage} />
              <Route path="/accounts" component={AccountsPage} />
              <Route path="/user-options" component={UserOptions} />
              <Route path="/manual" component={ManualPage} />
              <Route path="/thanks" component={ThankYouPage} />
              <Route path="/poweroff" component={PowerOffPage} />
              <Route path="/getstarted" component={GetStartedPage} />
              <Route path="/firsttimeuser" component={FirstTimeUserPage} />
              <Route path="/nutrienttesting" component={NutrientTestingPage} />
              <Route path="/selectnetwork" component={SelectNetworkPage} />
              <Route path="/barcode" component={BarCodePage} />
              <Route path="/analysis" component={AnalysisPage} />
              <Route path="/analyse" component={AnalysePage} />
              <Route path="/upload" component={UploadPage} />
              <Route path="/calibrate" component={CalibratePage} />
              <Route path="/settings" component={SettingsPage} />
              <Route path="/version" component={VersionPage} />
              <Route path="/brightness" component={BrightnessPage} />
              <Route path="/statistics" component={StatisticsPage} />
              <Route path="/guides" component={GuidePage} />
            </Switch>
          </BrowserRouter>
        </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
    return {
      accounts: state.user.accounts,
      active_account: state.user.active_account,
    };
  }

  export default connect(mapStateToProps, { getNetworkStatus })(
    App
  );



