import * as axios from "axios";

export const USER_LOGIN = "/user/login";
export const USER_LOGIN_ERROR = "/user/login_error";
export const USER_LOAD_WIFI = "/user/load_wifi";
export const USER_CONNECT_WIFI = "/user/connect_wifi";
export const USER_CONNECT_WIFI_ERROR = "/user/connect_wifi_error";
export const USER_GET_ACCOUNTS = "/user/get_accounts";
export const USER_SET_ACCOUNT = "/user/set_account";
export const USER_SET_OPTIONS = "/user/set_options";
export const USER_SET_OPTIONS_ERROR = "/user/set_options_error";
export const USER_SET_STATE = "/user/set_state";
export const USER_GET_NETWORK_STATUS = "/user/get_network_status";

export function login(username, password) {
  return function (dispatch) {
    var data = new FormData();
    data.append("username", username);
    data.append("password", password);

    return axios
      .post("/api/user/login", data)
      .then((response) => {
        return dispatch({
          type: USER_LOGIN,
          data: response.data,
        });
      })
      .catch((error) => {
        return dispatch({
          type: USER_LOGIN_ERROR,
          data: error.message,
        });
      });
  };
}

export function loadWifi() {
  return function (dispatch) {
    return axios
      .get("/api/user/wifi")
      .then((response) => {
        return dispatch({
          type: USER_LOAD_WIFI,
          data: response.data,
        });
      })
      .catch((error) => console.log(error));
  };
}

export function connectWifi(ssid, password) {
  return function (dispatch) {
    var data = new FormData();
    data.append("ssid", ssid);
    data.append("password", password);

    return axios
      .post("/api/user/wifi", data)
      .then((response) => {
        return dispatch({
          type: USER_CONNECT_WIFI,
          ssid: ssid,
        });
      })
      .catch((error) => {
        console.log("error");
        return dispatch({
          type: USER_CONNECT_WIFI_ERROR,
          ssid: ssid,
        });
      });
  };
}

export function getAccounts() {
  return function (dispatch) {
    return axios
      .get("/api/user/accounts")
      .then((response) => {
        return dispatch({
          type: USER_GET_ACCOUNTS,
          data: response.data,
        });
      })
      .catch((error) => console.log(error));
  };
}

export function setAccount(id, name) {
  return function (dispatch) {
    var data = new FormData();
    data.append("id", id);
    data.append("name", name);

    return axios
      .post("/api/user/set_account", data)
      .then((response) => {
        return dispatch({
          type: USER_SET_ACCOUNT,
          data: { id, name },
        });
      })
      .catch((error) => console.log(error));
  };
}

export function fetchOptions() {
  return function (dispatch) {
    return axios
      .get("/api/user/options")
      .then((response) => {
        return dispatch({
          type: USER_SET_OPTIONS,
          data: response.data,
        });
      })
      .catch((error) => {
        console.log('Error in fetchOptions :', JSON.stringify(error));
        return dispatch({
          type: USER_SET_OPTIONS_ERROR,
          data: error.response.data // This should save the response data of the server
        })
      });
  };
}

export function postOptions(state) {
  return function (dispatch) {
    var data = new FormData();
    data.append("model_type", state.model_type);
    data.append("use_barcode", state.use_barcode);

    return axios
      .post("/api/user/options", data)
      .then((response) => {
        return true;
      })
      .catch((error) => console.log(error));
  };
}

export function getUserState() {
  return function (dispatch) {
    return axios.get("/api/user/state")
    .then((response) => {
      return dispatch({
        type: USER_SET_STATE,
        data: response.data
      })
    })
    .catch((error) => console.log(error));
  }
}

export function getNetworkStatus() {
  return function (dispatch) {
    return axios.get("/api/user/network_status")
    .then((response) => {
      return dispatch({
        type: USER_GET_NETWORK_STATUS,
        data: response.data
      })
    })
    .catch((error) => console.log(error));
  }
}



