import React from "react";
import Keyboard from "react-virtual-keyboard";

import Popup from '../popup/Popup.js';

export default class Case8Modal extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
        sample_id: ''
    }

    this.handleSave = this.handleSave.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.onChnageKeyboard = this.onChnageKeyboard.bind(this);
  }

  onChnageKeyboard(value) {
    this.setState((old) => ({
        ...old,
        sample_id: value,
      }));
  }

  async handleSave() {
    this.props.onSave(this.state.sample_id);
  }

  async handleCancel() {
    this.props.onCancel();
  }

  render() {
    return (
      <Popup
        message={this.props.text}
        subMessage="Please enter a unique id"
        okLabel="Save"
        onClick={this.handleSave}
        onCancleClick={this.handleCancel}
        inputComponent={
          <Keyboard
            style={{ top: "137px" }}
            placeholder="Enter your sample_id here"
            className="form-field request-sample"
            value={this.state.sample_id}
            name="request-sample"
            options={{
              type: "text",
              usePreview: false,
              layout: "qwerty",
              autoAccept: true,
              alwaysOpen: false,
              appendLocally: false,
              updateOnChange: true,
              color: "light",
              enterNavigation: true,
              keyBinding: 'touchstart',
            }}
            onChange={this.onChnageKeyboard}
          />
        }
      />
    );
  }
}



