import React from 'react';
import {Link} from 'react-router-dom';
import { connect } from "react-redux";
import logo from '../../resources/logo.png';
import wifi from '../../resources/wifi.png';
import wifiRed from '../../resources/wifi-red.png';
import battery from '../../resources/battery.png';
import power from '../../resources/power.png';
import back from '../../resources/back.png';
import GlobalMessage from '../globalmessage/GlobalMessage';

class ManualPage extends React.Component {
  componentDidMount() {
    console.log(this.props);
  }
  render() {
    const { network_status } = this.props;

    return (
      <div>
        <img className="login-logo-barcode" src={logo} alt="Logo" styles={{width:'40%'}} />
        <img className="battery-alt" src={battery} alt="Battery" styles={{width:'40%'}} />
          <Link to="selectnetwork"><img className="wifi-signal" src={network_status === "OK" ? wifi : wifiRed}
            alt="Wifi" styles={{width:'40%'}} /></Link>
          <Link to="poweroff"><img className="power" src={power} alt="Power"
            styles={{width:'40%'}} /></Link>
        <label className="user-manual">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque sed diam ut est accumsan blandit. Suspendisse vestibulum dolor quis leo tempor, a blandit tellus rhoncus. Sed imperdiet massa est, quis semper mi maximus sed. Aliquam blandit ac massa eget pellentesque. Ut imperdiet luctus arcu, id fringilla nulla fringilla ut. Pellentesque consectetur ipsum ultricies nisi convallis consectetur. Sed semper dignissim turpis, interdum ornare metus vehicula ac. Suspendisse eleifend semper sodales.
        </label>
        <br></br>
        <label className="user-manual">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque sed diam ut est accumsan blandit. Suspendisse vestibulum dolor quis leo tempor, a blandit tellus rhoncus. Sed imperdiet massa est, quis semper mi maximus sed. Aliquam blandit ac massa eget pellentesque. Ut imperdiet luctus arcu, id fringilla nulla fringilla ut. Pellentesque consectetur ipsum ultricies nisi convallis consectetur. Sed semper dignissim turpis, interdum ornare metus vehicula ac. Suspendisse eleifend semper sodales.
        </label>
        <br></br>
        <label className="user-manual">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque sed diam ut est accumsan blandit. Suspendisse vestibulum dolor quis leo tempor, a blandit tellus rhoncus. Sed imperdiet massa est, quis semper mi maximus sed. Aliquam blandit ac massa eget pellentesque. Ut imperdiet luctus arcu, id fringilla nulla fringilla ut.
        </label>
        <Link to="firsttimeuser"><img className="back-button" src={back} alt="Back"
          styles={{width:'40%'}} /></Link>
        <GlobalMessage />
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    network_status: state.user.network_status,
  };
}

export default connect(mapStateToProps)(ManualPage);



